"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Logo from "../../components/logo";
import { motion } from "framer-motion";

export default function LogFoodPage() {
  const router = useRouter();

  const [cursor, setCursor] = useState({ x: 0, y: 0 });

  const [name, setName] = useState("");
  const [calories, setCalories] = useState("");
  const [category, setCategory] = useState("general");
  const [error, setError] = useState("");

  const [recentFoods, setRecentFoods] = useState([]);
  const [scannerOpen, setScannerOpen] = useState(false);
  const [scanStatus, setScanStatus] = useState("idle");

  // -------------------------------------------------------------------
  // Fetch last 5 foods
  // -------------------------------------------------------------------
  useEffect(() => {
    fetch("/api/food/recent")
      .then((res) => res.json())
      .then((data) => setRecentFoods(data.foods || []))
      .catch(() => {});
  }, []);

  // -------------------------------------------------------------------
  // Handle form submit
  // -------------------------------------------------------------------
  async function handleSubmit(e) {
    e.preventDefault();

    const res = await fetch("/api/food/log/route", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ name, calories, category }),
    });

    if (res.ok) {
      router.push("/dashboard");
    } else {
      setError("Failed to log food.");
    }
  }

  // -------------------------------------------------------------------
  // Fake Barcode Scan
  // -------------------------------------------------------------------
  function startFakeScan() {
    setScanStatus("scanning");

    setTimeout(() => {
      setScanStatus("success");

      // Autofill example (looks real enough)
      setName("Greek Yogurt (Plain)");
      setCalories("120");

      setTimeout(() => {
        setScannerOpen(false);
        setScanStatus("idle");
      }, 1200);
    }, 2000);
  }

  return (
    <main
      className="min-h-screen bg-slate-50 relative overflow-hidden"
      onMouseMove={(e) => setCursor({ x: e.clientX, y: e.clientY })}
    >
      {/* Cursor Reactive Glow */}
      <motion.div
        className="pointer-events-none fixed top-0 left-0 w-full h-full"
        animate={{
          background: `radial-gradient(450px circle at ${cursor.x}px ${cursor.y}px,
            rgba(59,130,246,0.18),
            transparent 70%)`,
        }}
        transition={{ duration: 0.15, ease: "linear" }}
      />

      {/* NAVBAR */}
      <nav className="w-full flex items-center justify-between px-8 py-4 bg-white shadow-sm relative z-10">
        <button
          onClick={() => router.push("/dashboard")}
          className="flex items-center gap-2 cursor-pointer"
        >
          <Logo className="h-8 w-8" />
        </button>

        <span className="text-slate-500 text-sm">Log Food</span>
      </nav>

      {/* PAGE CONTENT */}
      <div className="w-full flex justify-center mt-20 px-4 relative z-10">
        <div className="bg-white shadow-xl rounded-xl p-8 max-w-sm w-full border border-slate-200">

          <h1 className="text-2xl font-semibold text-slate-900 mb-6 text-center">
            Add a Food Entry
          </h1>

          {error && (
            <p className="text-red-600 text-sm mb-4 text-center">{error}</p>
          )}

          <form onSubmit={handleSubmit} className="flex flex-col gap-4">

            {/* FOOD NAME */}
            <div className="flex flex-col">
              <label className="text-gray-700 mb-1">Food Name</label>
              <input
                type="text"
                required
                className="border border-slate-300 rounded-lg px-3 py-2 focus:outline-none focus:border-blue-600"
                placeholder="Chicken Breast"
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </div>

            {/* CALORIES */}
            <div className="flex flex-col">
              <label className="text-gray-700 mb-1">Calories</label>
              <input
                type="number"
                required
                className="border border-slate-300 rounded-lg px-3 py-2 focus:outline-none focus:border-blue-600"
                placeholder="165"
                value={calories}
                onChange={(e) => setCalories(e.target.value)}
              />
            </div>

            {/* CATEGORY SELECT */}
            <div className="flex flex-col">
              <label className="text-gray-700 mb-1">Category</label>
              <select
                className="
                  border border-slate-300 rounded-lg px-3 py-2 
                  focus:outline-none focus:border-blue-600
                "
                value={category}
                onChange={(e) => setCategory(e.target.value)}
              >
                <option value="general">General</option>
                <option value="protein">Protein</option>
                <option value="carb">Carb</option>
                <option value="fat">Fat</option>
                <option value="veggie">Vegetable</option>
                <option value="fruit">Fruit</option>
                <option value="snack">Snack</option>
                <option value="drink">Drink</option>
              </select>
            </div>

            {/* SUBMIT BUTTON */}
            <button
              type="submit"
              className="
                bg-blue-700 hover:bg-blue-800 text-white py-2 rounded-lg 
                font-medium transition shadow-sm
              "
            >
              Add Entry
            </button>
          </form>

          {/* BARCODE SCAN BUTTON */}
          <button
            onClick={() => setScannerOpen(true)}
            className="mt-4 w-full text-blue-700 hover:underline text-sm"
          >
            Scan Barcode Instead →
          </button>

          {/* RECENT FOODS */}
          {recentFoods.length > 0 && (
            <div className="mt-8">
              <h3 className="text-sm text-slate-500 mb-3">
                Recent Foods
              </h3>

              <div className="flex flex-col gap-2">
                {recentFoods.map((food, idx) => (
                  <button
                    key={idx}
                    className="text-left px-3 py-2 bg-slate-100 hover:bg-slate-200 rounded-lg text-sm flex justify-between"
                    onClick={() => {
                      setName(food.name);
                      setCalories(food.calories);
                      setCategory(food.category || "general");
                    }}
                  >
                    <span>{food.name}</span>
                    <span className="text-slate-600">{food.calories} cal</span>
                  </button>
                ))}
              </div>
            </div>
          )}

        </div>
      </div>

      {/* BARCODE MODAL */}
      {scannerOpen && (
        <div className="fixed inset-0 bg-black/40 backdrop-blur-sm flex items-center justify-center z-20">
          <div className="bg-white p-6 rounded-xl shadow-xl w-80 text-center">

            <h2 className="text-xl font-semibold mb-4">Barcode Scanner</h2>

            <div className="w-full h-40 bg-slate-200 rounded-lg flex items-center justify-center text-slate-500">
              {scanStatus === "idle" && "Camera Initializing…"}
              {scanStatus === "scanning" && "Scanning… Hold steady…"}
              {scanStatus === "success" && "Barcode Found!"}
            </div>

            <button
              onClick={startFakeScan}
              disabled={scanStatus !== "idle"}
              className="
                mt-6 w-full bg-blue-700 hover:bg-blue-800 text-white py-2 rounded-lg 
                disabled:opacity-50
              "
            >
              Start Scan
            </button>

            <button
              onClick={() => setScannerOpen(false)}
              className="mt-3 text-sm text-slate-600 hover:underline"
            >
              Cancel
            </button>

          </div>
        </div>
      )}
    </main>
  );
}
